import React, { ReactNode } from 'react';
import { CheckboxIcon } from './CheckboxIcon';
import {
  CheckboxWrap,
  CheckboxInput,
  CheckboxText,
} from './styled';
import { useDefaultProp } from '../../common/react/useDefaultProp';

export interface ICheckboxProps {
  /**
   * 是否选中
   */
  checked?: boolean;
  /**
   * 默认是否选中
   */
  defaultChecked?: boolean;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 变化事件
   */
  onChange?: React.ChangeEventHandler<HTMLInputElement>
  /**
   * 模糊态，选中状态显示为短横线
   */
  indeterminate?: boolean;
  /**
   * 子元素
   */
  children?: ReactNode;
}

export const Checkbox = React.memo(
  React.forwardRef<HTMLInputElement, ICheckboxProps>(
    (props, ref) => {
      const {
        defaultChecked = false,
        checked: _checked,
        onChange,
        children,
        indeterminate = false,
        disabled = false,
      } = props;

      const [checked, setChecked] = useDefaultProp(
        defaultChecked,
        _checked,
        onChange,
        e => { return e.target.checked; }
      );

      return (
        <CheckboxWrap disabled={disabled} checked={checked}>
          <CheckboxInput
            ref={ref}
            type="checkbox"
            onChange={setChecked}
            disabled={disabled}
            checked={checked}
          />
          <CheckboxIcon disabled={disabled} checked={checked} indeterminate={indeterminate}/>
          {children && <CheckboxText>{children}</CheckboxText>}
        </CheckboxWrap>
      );
    }),
);
